<style >
  /*   @import '../styles/reset.css'; */


     
  .item {
  cursor: pointer;
}
.bold {
  font-weight: bold;
}
ul {
  padding-left: 1em;
  line-height: 1.5em;
  list-style-type: dot;
}
</style>
<template>
  <div  class="tree-content " >
    <div class="tree-content-main" v-select  >
      <div class="title">
        <span>移动到</span>
        <span  @click="closeTree" class="close_tree">X</span>
      </div>
      <div class="tree-folder-content">
      <ul>
        <item
        class="item"
        :model="treeData"></item>
        </ul>
      </div>
      <div class="tree-folder-buttons">
       
        <button type="button" class="btn btn-default ">确定</button>
        <button type="button" class="btn btn-default">取消</button>
      </div>

    </div>
  </div>

</template>
<script>
// define the item component
import 'jquery-slimscroll/jquery.slimscroll.min.js';
import '../directives/treePadding.js';





let  data = {
  name: 'My Tree',
  children: [
    { name: 'hello' },
    { name: 'wat' },
    {
      name: 'childkkkkkkkkkkkkk',
      children: [
        {
          name: 'child folder',
          children: [
            { name: 'hello' },
            { name: 'wat' }
          ]
        },
        { name: 'hello' },
        { name: 'wat' },
        {
          name: 'child folder',
          children: [
            { name: 'hello' },
            { name: 'wat' }
          ]
        }
      ]
    }
  ]
}



  export default {
          components: { 
            
                },
          ready() {
              
                  $('.tree-folder-content').slimscroll({
                          height: 'auto',
                          width: 'auto', //可滚动区域宽度
                          size: '4px', //组件宽度
                          opacity: .3, //滚动条透明度
                          distance: '14px', //组件与侧边之间的距离
                          railColor: '#333', //轨道颜色
                          railOpacity: .2, //轨道透明度
                          railDraggable: true, //是否 滚动条可拖动
                  });

                   // $(".tree-content-main").draggabilly()
             
             },
         props: [
         
        ],
        data: function () {
          return {
            treeData: data
          }
        },
        computed: {
          
        },
        methods: {
          closeTree(){
              this.$dispatch("closeTree")
          }
          
        }
  }
</script>


