    methods: {
      getresource( folderId ){
          console.log(this.user.name); // logs: 'cody'
         

//////////////////////////////修改name到app.vue/////////////////////////////////////

        
        this.$http.get('./FolderAction!GetFolderCotent?curFolderId='+folderId)
          .then(function(ret) {
              this.datas = JSON.parse(ret.data).files.concat(JSON.parse(ret.data).childFolders);
              
              this.files =  JSON.parse(ret.data).files;  
              this.childFolders = JSON.parse(ret.data).childFolders;
             
            
          })
          .then(function(err) {
           
          })
      },
      /*删除文件*/
      trashFiled (fileId,index){
          let r=confirm("您确定要删除文件吗")
            if (r==true)
              {
              }
               else
              {
               
              return false
              }
            

         this.$http.get('./FileInFolderAction!DeleteFile?id='+fileId)
          .then(function(ret) {
            console.log(ret) 
            this.files.splice(index,1)
          })
          .then(function(err) {
            console.log(err);
          })

     
      },
 

      /*删除文件夹*/
      trashFolder (folderId){
        alert('no use')
      },
  

