           this.mychosekey =true
         }else{
           this.mychosekey =false

         }

         //控制移动和重命名的按钮
        //取消移动和重命名的操作
        //重命名图标操作
         if(this.choseArr.length > 1){
         this.listoperation= true
         this.isdisabledIcon = true
         }else{
           this.listoperation= false
           this.isdisabledIcon = false
         }

